import React from "react"
import Box from "react-bulma-components/esm/components/box"
import Content from "react-bulma-components/esm/components/content"
import Tag from "react-bulma-components/esm/components/tag"
import NewsletterTitle from "./newsletterTitle"
import Emoji from "a11y-react-emoji"
import PropTypes from "prop-types"
import Obfuscate from "react-obfuscate"
import Slug from "slug"

const markUp = (data) => ({ __html: data })

function NewsletterBox(node) {
  return (
    <Box>
      <Content>
        <NewsletterTitle node={node.node} />
        <hr />
        <p>{node.node.newsletter.welcome}</p>
        <div dangerouslySetInnerHTML={markUp(node.node.newsletter.intro)} />
        {node.node.newsletter.parts.map((part, index) => (
          <div
            key={index}
            style={{ marginBottom: "40px" }}
            id={Slug(part.title)}
          >
            <h4>
              {index + 1}. {part.title}{" "}
              <Emoji symbol={part.emoji.icon} label={part.emoji.label} />
              <a href={`#${Slug(part.title)}`} className={"hanchor"}>
                <svg
                  aria-hidden="true"
                  height="20"
                  version="1.1"
                  viewBox="0 0 16 16"
                  width="20"
                >
                  <path
                    fill-rule="evenodd"
                    d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"
                  ></path>
                </svg>
              </a>
            </h4>
            <div style={{ marginBottom: "30px" }}>
              {part.text.map((text, i) => (
                <div key={i} dangerouslySetInnerHTML={markUp(text)} />
              ))}
            </div>
            <Tag.Group className={"text-tags"}>
              {part.tags.map((tag) => (
                <Tag key={tag}>#{tag}</Tag>
              ))}
            </Tag.Group>
          </div>
        ))}
        <hr />
        <div className={"outro"}>
          <div dangerouslySetInnerHTML={markUp(node.node.newsletter.outro)} />
          Bitte gerne weiterempfehlen !<br />
          <a href="https://ivos-ecotainment-newsletter.info">
            https://ivos-ecotainment-newsletter.info
          </a>
          <br />
          <br />
          Tracking ist aus!
          <br />
          Feedback, Forwards, Fails an{" "}
          <Obfuscate email="hello@ivos-ecotainment-newsletter.info" />
          <br />
          <br />
          You are welcome!
        </div>
      </Content>
    </Box>
  )
}

NewsletterBox.defaultProps = {
  node: {},
}

NewsletterBox.propTypes = {
  node: PropTypes.object,
}

export default NewsletterBox
